.visually-hidden {
	position: absolute !important;
	clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
	clip: rect(1px, 1px, 1px, 1px);
	overflow: hidden;

	padding: 0 !important;
	border: 0 !important;
	height: 1px !important;
	width: 1px !important;
}

.logo {
	color: #fff;

	font-family: "Big John";
	font-weight: 700;
	font-size: 25px;
}

.title {
	color: #333b46;

	text-transform: uppercase;
	font-family: $ffSS;
	font-size: 20px;
}
.text {
	line-height: 45 / 30 * 100%;
	color: #81868e;

	font-family: $ffOS;
	font-size: 14px;
}
.link {
	display: inline-block;
	color: #333b46;
	padding: 12px 30px;
	border: 1px solid #333b46;
	border-radius: 30px;

	text-align: center;
	font-family: $ffOS;
	font-size: 16px;

	transition: all 0.2s ease;
	&:hover {
		color: #ffffff;
		background-color: #000000;
	}
}
.color--green {
	color: #19bd9a;
	font-weight: 700;
}
.subtitle {
	color: #464646;
	font-family: $ffOS;
	font-size: 20px;
}

.icon-body {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	border-radius: 50%;
	background-color: #81868e;

	width: 54px;
	height: 54px;

	transition: all 0.2s ease;

	&:hover {
		background-color: #19bd9a;
	}
}
.icon-img {
	width: 30px;
	height: 30px;
}
