@charset "UTF-8";

@import url("https://fonts.googleapis.com/css?family=Open+Sans:regular,700");

@import url("https://fonts.googleapis.com/css?family=Roboto:300");

@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,300italic,regular,900");

* {
  margin: 0;
  padding: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

ol {
  list-style-position: inside;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
  margin: 0;
}

@font-face {
  font-family: "Big John";
  font-display: swap;
  src: url("../fonts/BIG JOHN.woff") format("woff"), url("../fonts/BIG JOHN.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}

html {
  min-height: 100%;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

body {
  position: relative;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
}

.wrapper {
  height: 100%;
}

.lock {
  overflow: hidden;
}

.container {
  max-width: 1200px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
}

.logo {
  color: #fff;
  font-family: "Big John";
  font-weight: 700;
  font-size: 25px;
}

.title {
  color: #333b46;
  text-transform: uppercase;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 20px;
}

.text {
  line-height: 150%;
  color: #81868e;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
}

.link {
  display: inline-block;
  color: #333b46;
  padding: 12px 30px;
  border: 1px solid #333b46;
  border-radius: 30px;
  text-align: center;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  transition: all 0.2s ease;
}

.link:hover {
  color: #ffffff;
  background-color: #000000;
}

.color--green {
  color: #19bd9a;
  font-weight: 700;
}

.subtitle {
  color: #464646;
  font-family: "Open Sans", sans-serif;
  font-size: 20px;
}

.icon-body {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: #81868e;
  width: 54px;
  height: 54px;
  transition: all 0.2s ease;
}

.icon-body:hover {
  background-color: #19bd9a;
}

.icon-img {
  width: 30px;
  height: 30px;
}

.header {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: url(../img/header/header-bg.jpeg) center/cover no-repeat;
}

.header::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.404);
  z-index: 1;
}

.header__info {
  position: relative;
  z-index: 2;
  text-align: center;
}

.header__title {
  color: #fff;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 300;
  font-size: 35px;
  letter-spacing: 5px;
  text-transform: uppercase;
  margin-bottom: 40px;
}

.header__promo {
  max-width: 600px;
  color: #fff;
  padding-bottom: 60px;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  line-height: 1.2;
}

.header__scroll-link {
  position: absolute;
  z-index: 3;
  bottom: 70px;
  left: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 37px;
  height: 37px;
  border-radius: 50%;
  background-color: #0a856a;
  transform: translateX(-50%);
  -webkit-animation: hoverWave linear 2.5s infinite;
  animation: hoverWave linear 2.5s infinite;
  transition: all 0.3s ease;
}

.header__scroll-link:hover {
  animation: hoverWaveBigger linear 2s infinite;
}

@-webkit-keyframes hoverWave {
  0% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 0 rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
  }

  40% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 15px rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
  }

  80% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 30px rgba(25, 189, 154, 0), 0 0 0 26.7px rgba(25, 189, 154, 0.067);
  }

  100% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 30px rgba(25, 189, 154, 0), 0 0 0 40px rgba(25, 189, 154, 0);
  }
}

@keyframes hoverWave {
  0% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 0 rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
  }

  40% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 15px rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
  }

  80% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 30px rgba(25, 189, 154, 0), 0 0 0 26.7px rgba(25, 189, 154, 0.067);
  }

  100% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 30px rgba(25, 189, 154, 0), 0 0 0 40px rgba(25, 189, 154, 0);
  }
}

@-webkit-keyframes hoverWaveBigger {
  0% {
    box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 0 rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
  }

  40% {
    box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 50px rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
  }

  80% {
    box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 60px rgba(25, 189, 154, 0), 0 0 0 66.7px rgba(25, 189, 154, 0.067);
  }

  100% {
    box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 65px rgba(25, 189, 154, 0), 0 0 0 80px rgba(25, 189, 154, 0);
  }
}

@keyframes hoverWaveBigger {
  0% {
    box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 0 rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
  }

  40% {
    box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 50px rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
  }

  80% {
    box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 60px rgba(25, 189, 154, 0), 0 0 0 66.7px rgba(25, 189, 154, 0.067);
  }

  100% {
    box-shadow: 0 30px 50px rgba(25, 189, 154, 0.3), 0 0 0 65px rgba(25, 189, 154, 0), 0 0 0 80px rgba(25, 189, 154, 0);
  }
}

.nav._active::before {
  background-color: #7c703a;
}

.nav._active .nav__body {
  height: 60px;
}

.nav {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 50;
  transition: all 0.2s ease;
}

.nav::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
  z-index: 2;
  transition: all 0.2s ease;
}

.nav__body {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 114px;
  align-items: center;
  transition: all 0.2s ease;
}

.nav__logo {
  position: relative;
  z-index: 3;
  padding-bottom: 10px;
  text-transform: uppercase;
}

.nav__burger {
  display: none;
}

.nav__list {
  display: flex;
}

.nav__list li {
  position: relative;
  z-index: 2;
}

.nav__list li:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  display: inline-block;
  color: #fff;
  padding: 5px 15px;
  border: 1px solid transparent;
  border-radius: 15px;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  transition: all 0.2s ease;
}

.nav__link:hover {
  border: 1px solid #fff;
}

.nav.nav--active::before {
  background: #4b4f14;
  background: -moz-linear-gradient(90deg, #4b4f14 0%, #885a05 64%);
  background: -webkit-linear-gradient(90deg, #4b4f14 0%, #885a05 64%);
  background: linear-gradient(90deg, #4b4f14 0%, #885a05 64%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#4b4f14",endColorstr="#885a05",GradientType=1);
}

.nav.nav--active .nav__body {
  height: 50px;
}

.promo__container {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  max-width: 850px;
  padding-top: 80px;
  padding-bottom: 50px;
}

.promo__info {
  text-align: justify;
  margin-right: 50px;
}

.promo__title {
  padding-bottom: 50px;
}

.promo__text {
  padding-bottom: 30px;
  line-height: 200%;
}

.promo__image {
  flex: 0 0 200px;
  align-self: flex-start;
}

.promo__img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.details {
  background-color: #f3f3f3;
  padding-top: 60px;
  padding-bottom: 80px;
}

.details__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.details__promo {
  text-align: center;
}

.details__title {
  margin-bottom: 40px;
  text-transform: uppercase;
}

.details__text {
  max-width: 470px;
  margin-bottom: 60px;
  margin-left: auto;
  margin-right: auto;
}

.details__icons {
  margin-bottom: 60px;
}

.details__icon:not(:last-child) {
  margin-right: 25px;
}

.ideas__subtitle {
  margin-bottom: 40px;
}

.ideas__text {
  display: flex;
  text-align: justify;
}

.ideas__text p {
  flex: 0 0 60%;
}

.ideas__text p:first-child {
  flex: 0 0 40%;
  padding-right: 130px;
}

.projects {
  padding-top: 60px;
}

.projects__promo {
  max-width: 460px;
  margin: 0 auto;
  margin-bottom: 60px;
  padding: 0 10px;
  text-align: center;
}

.projects__title {
  margin-bottom: 40px;
}

.projects__works {
  display: flex;
  width: 100%;
  align-items: stretch;
}

.projects__work:nth-child(3n + 1) {
  flex: 0 0 50%;
}

.projects__work:nth-child(3n + 2) {
  flex: 0 0 25%;
}

.projects__work:nth-child(3n + 3) {
  flex: 0 0 25%;
}

.projects__work {
  position: relative;
  z-index: 1;
  outline: none;
  display: inline-block;
}

.projects__work::after {
  content: "";
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  transition: background-color 0.2s ease-in;
}

.projects__work:hover::after {
  background-color: rgba(0, 0, 0, 0);
}

.projects__work:hover .projects__work-title {
  opacity: 0;
  visibility: hidden;
}

.projects__work:hover .projects__work-img {
  filter: none;
}

.projects__work:focus::after {
  background-color: rgba(0, 0, 0, 0);
}

.projects__work:focus .projects__work-title {
  opacity: 0;
  visibility: hidden;
}

.projects__work:focus .projects__work-img {
  filter: none;
}

.projects__work-img {
  width: 100%;
  height: 320px;
  object-fit: cover;
  filter: grayscale(100%);
  transition: filter 0.2s ease-in;
}

.projects__work-title {
  position: absolute;
  top: 50%;
  left: 50%;
  visibility: visible;
  z-index: 3;
  opacity: 1;
  color: #ffffff;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 20px;
  text-transform: uppercase;
  transform: translate(-50%, -50%);
  transition: all 0.2s ease-in;
}

.footer-project {
  position: relative;
  padding-top: 38px;
  padding-bottom: 38px;
  background: url(../img/projects/bg.jpeg) center/cover no-repeat;
}

.footer-project::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: #19bd9a;
  opacity: 0.7;
}

.footer-project__container {
  position: relative;
  z-index: 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-project__text {
  color: #ffffff;
  margin-right: 20px;
  text-align: justify;
  font-family: "Open Sans", sans-serif;
  font-size: 20px;
}

.footer-project__link-wrapp {
  flex-grow: 1;
  flex-shrink: 0;
  display: flex;
  justify-content: flex-end;
}

.footer-project__link {
  color: #ffffff;
  border: 1px solid #ffffff;
}

.footer-project__link:hover {
  color: #19bd9a;
  background-color: #fff;
}

.services {
  display: flex;
  flex-direction: column;
  padding-top: 60px;
}

.services__container {
  padding-bottom: 80px;
}

.services__promo {
  max-width: 460px;
  margin: 0 auto;
  margin-bottom: 60px;
  text-align: center;
}

.services__title {
  margin-bottom: 40px;
}

.services__row {
  display: flex;
}

.services__column {
  flex: 0 0 50%;
}

.graphic {
  display: flex;
}

.graphic__icons {
  margin-right: 50px;
}

.graphic__icon:not(:last-child) {
  margin-bottom: 16px;
}

.graphic__title {
  margin-bottom: 30px;
}

.graphic__text {
  text-align: justify;
}

.graphic__text p:not(:last-child) {
  margin-bottom: 20px;
}

.progress {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.progress__wrapper {
  display: none;
}

.progress__body {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.progress__body:not(:last-child) {
  margin-right: 40px;
}

.progress__title {
  color: #81868e;
  text-align: center;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
}

.progress__circle-wrap {
  width: 120px;
  height: 120px;
  background: #047378;
  border-radius: 50%;
  margin-bottom: 20px;
}

.progress__mask,
.progress .fill {
  width: 120px;
  height: 120px;
  position: absolute;
  border-radius: 50%;
}

.progress__mask {
  clip: rect(0px, 120px, 120px, 60px);
}

.progress .fill {
  clip: rect(0px, 60px, 120px, 0px);
  background-color: #19bd9a;
}

.progress .full60,
.progress .fill60 {
  animation: fill60 ease-in-out 2s;
  transform: rotate(108deg);
}

.progress .full75,
.progress .fill75 {
  animation: fill75 ease-in-out 2s;
  transform: rotate(135deg);
}

.progress .full80,
.progress .fill80 {
  animation: fill80 ease-in-out 2s;
  transform: rotate(144deg);
}

.progress__inside-circle {
  width: 110px;
  height: 110px;
  border-radius: 50%;
  background: #fff;
  line-height: 110px;
  text-align: center;
  margin-top: 5px;
  margin-left: 5px;
  position: absolute;
  z-index: 2;
  font-weight: 700;
  font-size: 30px;
}

@keyframes fill80 {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(144deg);
  }
}

@keyframes fill75 {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(135deg);
  }
}

@keyframes fill60 {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(108deg);
  }
}

.quotes {
  position: relative;
  padding-top: 80px;
  padding-bottom: 60px;
  z-index: 1;
}

.quotes::after {
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(../img/service/bg.jpeg) center/cover no-repeat;
  filter: grayscale(100%) brightness(25%);
}

.quotes__container {
  position: relative;
  z-index: 2;
  display: flex;
  justify-content: space-between;
}

.quotes__block-info {
  position: relative;
  z-index: 3;
  max-width: 600px;
}

.quotes__title {
  color: #f3f3f3;
  margin-bottom: 40px;
  font-family: "Source Sans Pro", sans-serif;
}

.quotes__text {
  color: #b2b3b3;
  margin-bottom: 35px;
}

.quotes__author {
  color: #b2b3b3;
  text-transform: uppercase;
}

.quotes__info {
  color: #b2b3b3;
  text-transform: uppercase;
}

.quotes__block--image {
  position: absolute;
  right: 10px;
  top: -120px;
  z-index: 2;
