.portfolio {
	padding-top: 80px;
	padding-bottom: 80px;
	background-color: #f3f3f3;
	&__container {
		max-width: 1110px;
	}

	&__promo {
		max-width: 450px;
		margin: 0 auto;
		margin-bottom: 80px;

		text-align: center;
	}

	&__title {
		margin-bottom: 40px;
	}

	&__text {
	}

	&__filter {
		margin-bottom: 40px;
	}

	&__banner {
		position: relative;
		z-index: 1;
		background: url(../img/portfolio/banner-bg.png) center/cover no-repeat;
		padding-top: 50px;
		padding-bottom: 20px;

		text-align: center;

		&::after {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			z-index: 1;

			width: 100%;
			height: 100%;
			background-color: $accent-color;
			opacity: 0.8;
		}
	}

	@media (max-width: $md3+px) {
		padding-top: 60px;
		padding-bottom: 60px;
		&__promo {
			margin-bottom: 30px;
		}
		&__title {
			margin-bottom: 20px;
		}
	}
}

.filter {
	&__buttons {
		margin-bottom: 40px;

		font-family: $ffOS;
		font-size: 16px;
		text-align: center;
	}
	&__button {
		position: relative;
		outline: none;

		display: inline-block;
		padding: 12px 10px;
		background-color: transparent;
		color: #464646;

		cursor: pointer;
		transition: transform 0.3s ease;
		&:not(:last-child) {
			margin-right: 20px;
		}
		&:hover {
			transform: scale(1.2);
		}

		&:focus {
			transform: scale(1.2);
			&::after {
				opacity: 1;
				transform: translateX(-50%);
			}
		}

		&::after {
			content: "";
			position: absolute;
			bottom: 0;
			left: 50%;
			opacity: 0;
			width: 60%;
			height: 2px;
			background-color: $accent-color;

			transform: translateX(-50%);

			transition: all 0.3s ease;
		}
		&.active {
			transform: scale(1.2);
			&::after {
				opacity: 1;
			}
		}
	}

	&__container {
		overflow: hidden;
		display: grid;

		grid-template: repeat(auto-fill, minmax(100px, 1fr)) / repeat(auto-fill, minmax(240px, 1fr));
		grid-gap: 10px;
		width: 100%;
	}

	&__block {
		display: none;
		background-color: #5e676e;
		color: #ffffff;

		&.show {
			display: block;
		}
	}
	@media (max-width: $md3+px) {
		&__buttons {
			margin-bottom: 20px;
		}
		&__button {
			padding: 5px 10px;
			&:not(:last-child) {
				margin-right: 10px;
				margin-bottom: 10px;
			}
		}
	}
	@media (max-width: $md4+px) {
		&__button {
			padding: 5px 5px;
		}
	}
}

.f-block {
	&:hover .f-block__info {
		opacity: 1;
		visibility: visible;
	}
	&__wrapper.--active .f-block__info {
		opacity: 1;
		visibility: visible;
	}
	&__wrapper {
		position: relative;
		width: 100%;
		height: 100%;
	}

	&__image-link {
		position: absolute;
		z-index: 1;
		width: 100%;
		height: 100%;
	}

	&__img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	&__info {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		flex-direction: column;
		padding: 20px 20px;
		z-index: 2;
		opacity: 0;
		visibility: hidden;
		background-color: rgba(0, 0, 0, 0.75);
		width: 100%;
		height: 100%;

		transition: all 0.3s ease;
	}

	&__link {
	}

	&__title {
		position: relative;

		display: inline-block;
		padding-bottom: 20px;
		margin-bottom: 20px;
		color: $accent-color;

		text-transform: uppercase;
		font-family: $ffSS;
		font-size: 20px;
		&::after {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;

			width: 40%;
			height: 2px;
			background-color: $accent-color;
		}
	}

	&__text {
		margin-bottom: 30px;
		color: #b2b3b3;

		font-family: $ffOS;
		font-size: 12px;
	}

	&__icons {
		display: flex;
		align-self: flex-start;
	}

	&__copy-btn {
		margin-right: 15px;

		svg {
			fill: #ffffff;
			transition: all 0.2s ease;
		}
		&:hover {
			svg {
				fill: $accent-color;
			}
		}
	}

	&__icon-link {
		svg {
			fill: #ffffff;
			transition: fill 0.2s ease;
		}
		&:hover {
			svg {
				fill: $accent-color;
			}
		}
	}
	&__icon-link.active {
		svg {
			fill: red;
		}
	}
	&__icon {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 5px;

		border-radius: 50%;
		background-color: rgba(0, 0, 0, 0.8);
	}
	@media (max-width: $md3+px) {
		&__info {
			opacity: 1;
			visibility: visible;
		}
	}
}

/* Снэк-бар - расположите ее внизу и в середине экрана */
.snackbar {
	visibility: hidden; /* Скрыто по умолчанию. Видимый по щелчку мыши */
	min-width: 250px; /* Установите минимальную ширину по умолчанию */

	background-color: #333; /* Черный цвет фона */
	color: #fff; /* Белый цвет текста */
	text-align: center; /* Центрированный текст */
	border-radius: 2px; /* Округленные границы */
	padding: 16px; /* Отступ */
	position: fixed; /* Сядьте на верхнюю часть экрана */
	z-index: 10; /* Добавить z-индекс при необходимости */
	right: 5%; /* Центр, снэк-бар */
	bottom: 30px; /* 30 пикселей снизу */
}
.banner {
	&__text {
		position: relative;
		z-index: 2;

		color: #fff;
		margin-bottom: 20px;

		font-size: 20px;
	}

	&__link {
		position: relative;
		z-index: 2;

		color: #fff;
		margin-bottom: 30px;
		border: 1px solid #ffffff;
		&:hover {
			color: $accent-color;
			background-color: #ffffff;
			border: 1px solid #ffffff;
		}
	}
}
/* Показывать снэк-бар при нажатии на кнопку (Класс добавлен с помощью JavaScript) */
.snackbar.show-snackbar {
	visibility: visible; /* Покажите снэк-бар */
	/* Добавить анимацию: Возьмите 0,5 секунды, чтобы исчезать и выходить из закусочной.
	Однако задержите процесс затухания на 2,5 секунды */
	-webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
	animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

/* Анимация, чтобы исчезать снэк-бар в и из */
@-webkit-keyframes fadein {
	from {
		bottom: 0;
		opacity: 0;
	}
	to {
		bottom: 30px;
		opacity: 1;
	}
}

@keyframes fadein {
	from {
		bottom: 0;
		opacity: 0;
	}
	to {
		bottom: 30px;
		opacity: 1;
	}
}

@-webkit-keyframes fadeout {
	from {
		bottom: 30px;
		opacity: 1;
	}
	to {
		bottom: 0;
		opacity: 0;
	}
}

@keyframes fadeout {
	from {
		bottom: 30px;
		opacity: 1;
	}
	to {
		bottom: 0;
		opacity: 0;
	}
}
