.details {
	background-color: #f3f3f3;
	padding-top: 60px;
	padding-bottom: 80px;
	&__container {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	&__promo {
		text-align: center;
	}

	&__title {
		margin-bottom: 40px;

		text-transform: uppercase;
	}

	&__text {
		max-width: 470px;
		margin-bottom: 60px;
		margin-left: auto;
		margin-right: auto;
	}

	&__icons {
		margin-bottom: 60px;
	}
	&__icon {
		&:not(:last-child) {
			margin-right: 25px;
		}
	}

	@media (max-width: $md3+px) {
		padding-bottom: 60px;
		&__text {
			margin-bottom: 30px;
		}
		&__icons {
			margin-bottom: 40px;
		}
	}
}

.ideas {
	&__subtitle {
		margin-bottom: 40px;
	}

	&__text {
		display: flex;

		text-align: justify;
		p {
			flex: 0 0 60%;
		}
		p:first-child {
			flex: 0 0 40%;
			padding-right: 130px;
		}
	}
	@media (max-width: $md2+px) {
		&__text {
			p {
				flex: 0 0 50%;
			}
			p:first-child {
				flex: 0 0 50%;
				padding-right: 50px;
			}
		}
	}
	@media (max-width: $md3+px) {
		&__subtitle {
			margin-bottom: 20px;
		}
		&__text {
			flex-direction: column;
			p {
				flex: 0 0 50%;
			}
			p:first-child {
				padding-right: 0px;
				margin-bottom: 30px;
			}
		}
	}
}
