.services {
	display: flex;
	flex-direction: column;
	padding-top: 60px;

	&__container {
		padding-bottom: 80px;
	}
	&__promo {
		max-width: 460px;
		margin: 0 auto;
		margin-bottom: 60px;

		text-align: center;
	}

	&__title {
		margin-bottom: 40px;
	}

	&__row {
		display: flex;
	}

	&__column {
		flex: 0 0 50%;
	}

	@media (max-width: $md3+px) {
		&__title {
			margin-bottom: 20px;
		}
		&__row {
			flex-wrap: wrap;
		}
		&__promo {
			margin-bottom: 30px;
		}
		&__column {
			flex: 0 0 100%;
			&:not(:last-child) {
				margin-bottom: 40px;
			}
		}
	}
}

.graphic {
	display: flex;

	&__icons {
		margin-right: 50px;
	}

	&__icon {
		&:not(:last-child) {
			margin-bottom: 16px;
		}
	}

	&__title {
		margin-bottom: 30px;
	}

	&__text {
		text-align: justify;
		p:not(:last-child) {
			margin-bottom: 20px;
		}
	}

	@media (max-width: 1200px) {
		&__icons {
			margin-right: 20px;
		}
	}

	@media (max-width: $md2+px) {
		flex-direction: column;
		flex: 0 0 80%;
		&__icons {
			align-self: center;
			margin-right: 0;
		}
		&__icon {
			&:not(:last-child) {
				margin-right: 20px;
			}
		}
	}
	@media (max-width: $md3+px) {
		flex-direction: column;
		&__icons {
			align-self: center;
			margin-right: 0;
		}
		&__icon {
			&:not(:last-child) {
				margin-right: 20px;
			}
		}
	}
	@media (max-width: $md4+px) {
		&__icon {
			&:not(:last-child) {
				margin-right: 10px;
			}
		}
	}
}

.progress {
	display: flex;
	justify-content: space-around;
	align-items: center;
	&__wrapper {
		display: none;
	}

	&__body {
		display: flex;
		flex-direction: column;
		align-items: center;
		&:not(:last-child) {
			margin-right: 40px;
		}
	}
	&__title {
		color: #81868e;

		text-align: center;
		font-family: $ffRob;
		font-size: 16px;
	}
	&__circle-wrap {
		width: 120px;
		height: 120px;
		background: #047378;
		border-radius: 50%;
		margin-bottom: 20px;
	}

	&__mask,
	.fill {
		width: 120px;
		height: 120px;
		position: absolute;
		border-radius: 50%;
	}

	&__mask {
		clip: rect(0px, 120px, 120px, 60px);
	}

	.fill {
		clip: rect(0px, 60px, 120px, 0px);
		background-color: $accent-color;
	}

	.full60,
	.fill60 {
		animation: fill60 ease-in-out 2s;
		transform: rotate(108deg);
	}

	.full75,
	.fill75 {
		animation: fill75 ease-in-out 2s;
		transform: rotate(135deg);
	}

	.full80,
	.fill80 {
		animation: fill80 ease-in-out 2s;
		transform: rotate(144deg);
	}
	&__inside-circle {
		width: 110px;
		height: 110px;
		border-radius: 50%;
		background: #fff;
		line-height: 110px;
		text-align: center;
		margin-top: 5px;
		margin-left: 5px;
		position: absolute;
		z-index: 2;
		font-weight: 700;
		font-size: 30px;
	}
	@media (max-width: $md2+px) {
		flex: 0 0 20%;
		&__wrapper {
			flex-direction: column;
		}
		&__mask {
			clip: rect(0px, 80px, 80px, 40px);
		}

		.fill {
			clip: rect(0px, 40px, 80px, 0px);
		}
		&__circle-wrap {
			width: 80px;
			height: 80px;
			margin-bottom: 5px;
		}
		&__mask,
		.fill {
			width: 80px;
			height: 80px;
		}
		&__inside-circle {
			width: 70px;
			height: 70px;
			line-height: 70px;
			font-size: 24px;
		}
		&__body {
			&:not(:last-child) {
				margin-right: 0px;
				margin-bottom: 15px;
			}
		}
	}
	@media (max-width: $md3+px) {
		&__wrapper {
			flex-direction: row;
		}

		&__body {
			&:not(:last-child) {
				margin-right: 30px;
				margin-bottom: 0px;
			}
		}
	}
	@media (max-width: $md4+px) {
		&__wrapper {
			flex-direction: row;
		}

		&__body {
			&:not(:last-child) {
				margin-right: 10px;
				margin-bottom: 0px;
			}
		}
	}
}

@keyframes fill80 {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(144deg);
	}
}
@keyframes fill75 {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(135deg);
	}
}
@keyframes fill60 {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(108deg);
	}
}

.quotes {
	position: relative;
	padding-top: 80px;
	padding-bottom: 60px;
	z-index: 1;

	&::after {
		content: "";
		position: absolute;
		z-index: 1;
		top: 0;
		left: 0;

		width: 100%;
		height: 100%;
		background: url(../img/service/bg.jpeg) center/cover no-repeat;
		filter: grayscale(100%) brightness(25%);
	}
	&__container {
		position: relative;
		z-index: 2;
		display: flex;
		justify-content: space-between;
	}

	&__block-info {
		position: relative;
		z-index: 3;

		max-width: 600px;
	}

	&__title {
		color: #f3f3f3;
		margin-bottom: 40px;

		font-family: $ffSS;
	}

	&__text {
		color: #b2b3b3;
		margin-bottom: 35px;
	}

	&__author {
		color: #b2b3b3;
		text-transform: uppercase;
	}

	&__info {
		color: #b2b3b3;
		text-transform: uppercase;
	}

	&__block--image {
		position: absolute;
		right: 10px;
		top: -120px;
		z-index: 2;

		width: 400px;
		height: 300px;

		text-align: right;
	}
	&__image {
		max-width: 100%;
		max-height: 100%;
		object-fit: contain;
	}
	@media (max-width: 1037px) {
		&__block--image {
			width: 300px;
			height: 250px;
		}
		&__image {
		}
	}
	@media (max-width: $md2+px) {
		&__block-info {
			max-width: 450px;
		}
	}

	@media (max-width: $md3+px) {
		&__block--image {
			display: none;
		}
		&__block-info {
			max-width: none;
		}
		&__title {
			margin-bottom: 30px;
		}
	}
}
